.single-title
{
	h1
	{
		font-family: $triangle-font-headline;
		font-size: 42px;
		font-style: italic;
	}
	
	h2
	{
		color: grey;
		font-family: $triangle-font-headline;
		font-size: 20px;
		margin-top: -30px;
	}
}

.single-meta
{
	align-items: flex-start;
	border-bottom: 1px solid lightgrey;
	border-top: 1px solid lightgrey;
	display: flex;
	min-height: 50px;
	
	.single-author
	{
		font-family: $triangle-font-main;
		font-size: 12px;
		height: 50px;
		line-height: 50px;
		
		a
		{
			color: black;
			font-weight: 700;
			text-decoration: none;
		}
	}
	
	.single-date
	{
		font-family: $triangle-font-main;
		font-size: 12px;
		height: 50px;
		line-height: 50px;
		padding-left: 15px;
	}

	.single-share
	{
		align-self: flex-end;
		//font-family: $triangle-font-main;
		//font-size: 12px;
		height: 50px;
		line-height: 50px;
		margin-left: auto;
	}
}

.single-return
{
	margin-bottom: 15px;
	margin-top: 15px;

	a
	{
		color: lightskyblue;
		font-size: 16px;
		text-decoration: underline;
	}
}

.single-content
{
	overflow: hidden;
	width: 100%;
	
	p,
	ol,
	ul
	{
		font-family: $triangle-font-copy;
		font-size: 20px;
		margin: auto;
		max-width: 708px;
		padding-bottom: 20px;
		width: 100%;
	}
	
	li
	{
		padding-bottom: 10px;
		
		span
		{
			padding-left: 5px;
		}
	}
	
	p
	{
		img
		{
			height: auto;
			margin-left: 0px;
			margin-right: 0px;
			margin-bottom: 15px;
			width: 100% !important;
		}
	}
	
	figure
	{
		margin-bottom: 15px;
		margin-left: 0px;
		margin-right: 0px;
		margin-top: 0px;
		width: 100% !important; // Must be !important to override WordPress sizing
		
		img
		{
			height: auto;
			max-width: 100%;
			width: 100%;
		}
		
		figcaption
		{
			color: grey;
			font-family: $triangle-font-copy;
			font-size: 16px;
			font-style: italic;
		}
	}

	blockquote
	{
		margin: auto !important; // Using !important to override Instagram styling
	}
}

.single-comments
{
	margin: auto;
	max-width: 800px;
	overflow: hidden;
	width: 90%;
}

@media screen and (max-width: $screen-target-min)
{
	.single-title
	{
		h1 { font-size: 28px; }
		h2 { font-size: 15px; }
	}
	
	.single-meta
	{
		.date { float: right; }
	}
}